
.page-list{
  background:#f0f0f0;
}
.tab-city{
  display:flex;
}
.tab-city .mint-button {
  flex:1;
  background:#fff;
  border-radius:0;
}
.tab-city .v-modal{
  top: 86px;
}
.mint-popup{
  width: 100%;
  height: 250px;
  padding: 0;
  transform: translate(-50%, 0);
  overflow:scroll;
}
.mint-popup ul{
  list-style:none;
  margin:10px 0;
  padding:0 20px;
}
.mint-popup ul li{
  height:40px;
  line-height:40px;
  color:#828282;
}
.mint-popup ul .active{
  color: #0977C5;
}
.page-loadmore-list{
  list-style:none;
  margin:0;
  padding:0;
}
.page-loadmore-item{
  list-style:none;
  margin:10px 0;
  padding:20px 15px;
  background: #FFFFFF;
  border-top: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
}
.page-loadmore-item .item-foot{
  margin:20px 0 0 0;
  padding: 15px 0 0 0;
  border-top: 1px dashed #E9E9E9;
  list-style:none;
  display:flex;
}
.page-loadmore-item .item-foot li{
  margin-right:20px;
  font-size:15px;
  color:#828282;
}
.page-loadmore-item .item-foot li span{
  margin:0 2px;
  vertical-align: middle;
}
.page-loadmore-item .icon-location{
  width: 15px;
  height: 15px;
  margin:0;
  background:url(../../images/location_grey_icon@3x.png) 0% 100% no-repeat;
  background-size: 15px auto;
}
.page-loadmore-item .icon-gift{
  width: 15px;
  height: 15px;
  margin:0;
  background:url(../../images/welfare_grey_icon@3x.png) 0% 100% no-repeat;
  background-size: 15px auto;
}
.mint-loadmore-bottom span {
    display: inline-block;
    transition: .2s linear;
    vertical-align: middle;
}
.mint-loadmore-bottom span.rotate {
    transform: rotate(180deg);
}




