
.page-post{
  background:#f0f0f0;
  padding-bottom:50px;
}
.part-time-title{
  margin:0 15px;
  padding:15px 0;
  border-bottom: 1px dashed #D7D7D7;
}
.list-claim li{
  background-image: linear-gradient(0,#d9d9d9,#d9d9d9 50%,transparent 0);
  background-size: 120% 1px;
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-origin: content-box;
  padding:0;
  height:50px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.list-claim li label{
  width:120px;
  padding-left:10px;
}
.list-claim li label i{
  margin-top:-8px;
  margin-right:10px;
}
.list-claim li label,.list-claim li span{
  margin:15px 0;
}
.fix-signup{
  display:flex;
}
.fix-signup .mint-button--normal{
  box-shadow:none;
  flex: 2;
  font-size:16px;
}
.fix-signup .mint-button--primary{
  background: #0977C5;
  box-shadow:none;
  flex: 3;
  font-size:16px;
}




