
.page-post{
  background:#f0f0f0;
  padding-bottom:50px;
}
.post-info{
  padding:20px 15px;
  background:#fff;
}
.post-info-foot{
  margin-top:15px;
}
.post-info-foot button{
  margin-right:12px;
  border: 1px solid #D7D7D7;
  box-shadow:none;
  color: #828282;
}
.fix-post{
  display:flex;
}
.fix-post .mint-button--normal{
  flex: 1;
  background:#fff;
  font-size:14px;
  border-right:1px solid #E9E9E9;
  box-shadow:none;
  font-size:13px;
}
.fix-post .mint-button--normal label{
  display:block;
}
.fix-post .mint-button--primary{
  flex: 2;
  background: #0977C5;
  box-shadow:none;
  font-size:16px;
}



