.orange{
  color: #F9621D!important;
  font-family: "微软雅黑"!important;
}
.tl{
  text-align: left!important;
}


.mint-header{
  height: 44px;
  background: #0977C5;
  font-size: 17px;
}
.mint-header .mintui{
  font-size: 18px;
  font-weight: bold;
}

.fix-bottom{
  z-index: 2000;
  width: 100%;
  left: auto;
  right: auto;
  bottom: 0;
  position: fixed;
  background: #fff;
}
.fix-bottom .mint-button{
  height: 50px;
  font-size: 16px;
  border-radius: 0;
}
.fix-bottom .mint-button--large{
  background: #0977C5;
  box-shadow: 0 -1px 2px 0 rgba(0,0,0,0.12);
}


/*信息列表*/
.list-info{
  margin:0;
  padding:0;
}
.list-info li{
  display: flex;
  padding: 5px 0;
}

.list-info li label{
  width:76px;
  height:20px;
  text-align:right;
  color:#828282;
}

.list-info li span {
  flex: 1;
  margin-left:5px;
  color:#1E1E1E;
}

/*容器*/
.box{
  display:block;
  margin-bottom:10px;
  background:#fff;
  border-top:1px solid #E9E9E9;
  border-bottom:1px solid #E9E9E9;
}
.box-desc{
  margin:0;
  padding:10px 15px;
  font-size:15px;
  line-height:20px;
  color: #828282;
}
.box-desc p{
  margin:0;
  padding:4px 0;
}

/*图标*/
.common-icon{
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 25px;
  text-align: center;
  margin:0 5px;
}
.icon-loc{
  background:url(../../images/location_blue_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-money{
  background:url(../../images/money_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-info{
  background:url(../../images/information_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-welfare-blue{
  background:url(../../images/welfare_blue_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-condition{
  background:url(../../images/condition_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-attention{
  background:url(../../images/attention_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}

.icon-house{
  background:url(../../images/house_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-user{
  background:url(../../images/admin_blue_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-time{
  background:url(../../images/time_1_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}
.icon-time2{
  background:url(../../images/time_2_icon@3x.png) 0% 100% no-repeat;
  background-size: 18px auto;
}





