
.mix-box{
  display: flex;
}
.mix-box .mix-pic{
  width:60px;
  height:60px;
  border-radius: 30px;
}
.mix-box .mix-info{
  flex: 1;
  margin-left:10px;
  color: #1E1E1E;
}
.mix-box .mix-title{
  margin:10px 0;
  font-size: 17px;
  line-height: 20px;
}
.mix-box .mix-desc{
  margin:0;
  font-size: 15px;
  line-height: 16px;
}




